//font-size (1rem = 1*px)
$base-font-size: 8px;
$h1: $base-font-size * 8;
$h2: $base-font-size * 6;
$h3: $base-font-size * 4;
$h4: $base-font-size * 3;
$h5: $base-font-size * 2.5;  //20
$h6: $base-font-size * 2;
$h7: $base-font-size * 1.75;  //14

$base-line-height: 1.5;

html, body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
  "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: $base-font-size;  
  // font-size: 16px;
  line-height: 1.5;
}
h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
}
.h1 {
  font-size: 8rem; //64
  font-weight: bold;
  @include breakpoint(pad) {
		font-size: 6rem;
	}
	@include breakpoint(phone) {
		font-size: 4rem;
	}
}

