// box-sizing reset 
*,*::before,*::after {
	box-sizing: border-box;
}
html {
    transition: 0.5s all;
}
a {
    text-decoration: none;
    :hover {
        position: relative; /* 當滑鼠經過超連結時，超連結往右下位移1像素 */
        top: 1px;
        left: 1px;
    }
}
img {
    display: block;
    max-width: 100%;
    height: auto;
}
// 無邊框
input:focus{
    outline: none;
  }